import {
  Component,
  Input,
  OnChanges,
  SimpleChanges,
  ChangeDetectionStrategy
} from '@angular/core';
import { Map, Set } from 'immutable';

import { AbstractTrackerComponent } from '../abstract-tracker';
import { DomUtilService, PathUtilService } from '../shared/services';
import { JSONSchema } from '../shared/interfaces';


export class TreeMenuComponent extends AbstractTrackerComponent implements OnChanges {

   record: Map<string, any>;
   schema: JSONSchema;

  keys: Set<string>;

  private prefixOrPath = '';

  constructor(private domUtilService: DomUtilService,
    private pathUtilService: PathUtilService) {
    super();
  }

  ngOnChanges(changes: SimpleChanges) {
    let recordChange = changes['record'];
    if (recordChange) {
      let currentRecord: Map<string, any> = recordChange.currentValue;
      this.keys = currentRecord.keySeq().toSet();
    }
  }

  getChildPath(key: string) {
    return `${this.pathUtilService.separator}${key}`;
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'tree-menu',
  styles: ["div.tree-menu-container { padding: 8px 0; overflow-x: hidden; overflow-y: auto; text-align: center; } ul.menu-item-container { list-style: none; text-align: left; padding-top: 8px; } "],
  template: "<div class=\"tree-menu-container\"> <ul class=\"menu-item-container\"> <li *ngFor=\"let key of keys | addAlwaysShowFields:schema | filterAndSortBySchema:schema; trackBy:trackByElement\"> <tree-menu-item [label]=\"key\" [value]=\"record.get(key)\" [schema]=\"schema.properties[key]\" [path]=\"getChildPath(key)\" [depth]=\"1\"></tree-menu-item> </li> </ul> </div>",
  changeDetection: ChangeDetectionStrategy.OnPush
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: DomUtilService, },
{type: PathUtilService, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'record': [{ type: Input },],
'schema': [{ type: Input },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}



