import { Component, EventEmitter, Input, OnInit, Output, ChangeDetectionStrategy, TemplateRef } from '@angular/core';
import { Observable } from 'rxjs/Observable';

import 'rxjs/add/observable/of';

import { JsonStoreService, RemoteAutocompletionService, AppGlobalsService } from '../shared/services';
import { AutocompletionConfig, AutocompletionResult } from '../shared/interfaces';


export class AutocompleteInputComponent implements OnInit {

   autocompletionConfig: AutocompletionConfig;
   path: Array<any>;
   value: string;
   pathString: string;
   tabIndex: number;
   placeholder: string;

   onValueChange: EventEmitter<string> = new EventEmitter<any>();
   onKeypress: EventEmitter<KeyboardEvent> = new EventEmitter<any>();
   onBlur: EventEmitter<any> = new EventEmitter<any>();

  dataSource: Observable<string> | Array<string>;
  typeaheadOptionField: string;

  constructor(public remoteAutocompletionService: RemoteAutocompletionService,
    public jsonStoreService: JsonStoreService,
    public appGlobalsService: AppGlobalsService) { }

  ngOnInit() {
    // if url option set then use remote autocompletion service
    if (this.autocompletionConfig.url) {
      this.typeaheadOptionField = 'text';
      this.dataSource = Observable.create((observer: any) => {
        if (this.value && this.value.length > 0) {
          observer.next(this.value);
        }
      }).mergeMap((token: string) =>
        this.remoteAutocompletionService.getAutocompletionResults(this.autocompletionConfig, token));
    } else {
      this.dataSource = this.autocompletionConfig.source;
    }

  }

  get customItemTemplate(): TemplateRef<any> {
    return this.appGlobalsService.templates[this.autocompletionConfig.itemTemplateName];
  }

  onModelChange(value: string) {
    this.value = value;
    this.onValueChange.emit(value);
  }

  onCompletionSelect(completionItem: AutocompletionResult) {
    let onCompletionSelect = this.autocompletionConfig.onCompletionSelect;
    // if callback set and it is remote autocompletion source
    if (onCompletionSelect && this.typeaheadOptionField) {
      // .slice() is used to pass by value instead of reference
      onCompletionSelect(this.path.slice(), completionItem, this.jsonStoreService);
    }
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'autocomplete-input',
  styles: [""],
  template: "<div class=\"autocomplete-container\"> <input attr.data-path=\"{{pathString}}\" [ngModel]=\"value\" (ngModelChange)=\"onModelChange($event)\" (keypress)=\"onKeypress.emit($event)\" (blur)=\"onBlur.emit()\" [typeahead]=\"dataSource\" [typeaheadOptionsLimit]=\"autocompletionConfig.size\" [typeaheadOptionField]=\"typeaheadOptionField\" [typeaheadItemTemplate]=\"customItemTemplate\" (typeaheadOnSelect)=\"onCompletionSelect($event.item)\" [typeaheadWaitMs]=\"200\" [tabindex]=\"tabIndex\" placeholder=\"{{placeholder}}\"> </div>",
  changeDetection: ChangeDetectionStrategy.OnPush
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: RemoteAutocompletionService, },
{type: JsonStoreService, },
{type: AppGlobalsService, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'autocompletionConfig': [{ type: Input },],
'path': [{ type: Input },],
'value': [{ type: Input },],
'pathString': [{ type: Input },],
'tabIndex': [{ type: Input },],
'placeholder': [{ type: Input },],
'onValueChange': [{ type: Output },],
'onKeypress': [{ type: Output },],
'onBlur': [{ type: Output },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}



